a {
  color: #00bcd4;
  text-decoration: none;
}

img{
  max-width: 100%;
}


ul li {
  list-style: square;
}

.home {
  background: url(../assets/images/website-bg-1.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 0;
  padding-top: 46.190225959%; /* (img-height / img-width * container-width) */
  /* (879 / 1903 * 100) */
}



.training-yard-img {
  background: url(../assets/images/medieval-training-yard.png);
  background-repeat: no-repeat;
  background-size: 100%;
}

.shop-img {
  background: url(../assets/images/Shop.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.home-content {
  position: absolute;
  top: 0;
  width: 100%;
}

.logo {
  width:100%;
  max-width: 500px;
  margin-top: 3em;
}

.logo-text {
  font-family: Zombie;
  font-size: 8rem;
  text-shadow: -1px 5px 0px #a52121;
  letter-spacing: 8px;
  padding-top: 1em;
  margin-bottom: 0;
}

.slogan {
  margin-top: -15px;
  font-family: 'Lacquer', cursive;
  text-shadow: 0px 3px 0px rgb(0, 0, 0);
}

.section-heading {
  font-family: 'Nova Cut', cursive;
}

.banner-text {
  color:#fff;
  padding: 5px;
  padding-top:1rem;
}

.banner-mobile-extended {
  padding-top: 0;
  background: #000;
}

@media only screen and (max-width: 1366px) {
  .logo {
    margin-top: 0;
  }
  .logo-text {
    padding-top: 0.25em;
    font-size: 3.5rem;
  }

  .slogan {
    font-size: 2rem;
  }
}


.footer{
  position: relative;
}

.footer-image-left{
  position: absolute;
  left:0;
  bottom:0;
}

.footer-image-right{
  position: absolute;
  right:0;
  bottom:0;
}

.button-style{

  color: white;
  text-shadow: 1px 1px 1px #000;
  background: #832626;
}
.button-style:hover{
  background: #a73f3f;
}

.button-style-alt{
 
  color: white;
  text-shadow: 1px 1px 1px #000;
  background: #2b4777;
}
.button-style-alt:hover{
  background: #315085;
}


@media only screen and (max-width: 768px) {
  .logo-text {
    font-size: 3rem;
  }

  .slogan {
    font-size: 1.5rem;
  }

  .home {
    background-image: url(../assets/images/website-bg-1-mobile.png);
    padding-top: 95.8515283843%; /* (img-height / img-width * container-width) */
    /* (878 / 916 * 100) */
  }
  .logo {
    margin-top: 0;
  }

  .banner-buttons{
    padding-top: 1rem;
    margin-top: 0;
  }
}